/* ===========================
  #SCROLL  ON/OFF
============================= */


let getFixed = function() {
  let all = document.body.querySelectorAll('*');

  return  Array.from(all).filter(elem => {
    let bool = getComputedStyle(elem).position === 'fixed';
    return bool;
  })
}

let fixedElements = getFixed();

let scrollOn = function() {
  setTimeout(()=> {

    document.body.classList.remove('scroll-off');
    document.body.style.paddingRight = '';
    fixedElements.forEach(elem => {
      elem.style.paddingRight = ``; 
    })
  }, 300);
}

  let scrollOf = function() {
    document.body.classList.add('scroll-off');
    document.body.style.paddingRight = `${scrollW}px`;
    fixedElements.forEach(elem => {elem.style.paddingRight = `${scrollW}px`});
  }


/* ===========================
  #BURGER / DROP MENU
============================= */


let burger      = document.querySelector('.burger'),
    menuOverlay = document.createElement('div'),
    scrollW     = window.innerWidth - document.documentElement.clientWidth;

//Обробляе навігацію через клавішу ТАВ
document.addEventListener('keyup', (e) => {
  if(!document.activeElement.closest('.drop-content')) {
    // Фокус покинув останній елемент і преходить знов на burger
    if(e.code === 'Tab' && dropMenu.classList.contains('is-visible')) {
      burger.focus();
    }
  }
})

window.addEventListener('resize', function (e) {
  
  if(innerWidth >= 1100 && burger.classList.contains('is-active')) {
    menuOverlay.classList.remove('is-visible');
    burger.classList.toggle('is-active');
    dropMenu.classList.toggle('is-visible');
    scrollOn();
    
    setTimeout(()=> { 
      e.preventDefault();
      menuOverlay.remove(); 
    }, 500);
  }
})

burger.addEventListener('click', function(e) {
  scrollW = window.innerWidth - document.documentElement.clientWidth;
  
  burger.classList.toggle('is-active');
  dropMenu.classList.toggle('is-visible');

  if(dropMenu.classList.contains('is-visible')) {
    // Додаю overlay
    dropMenu.after(menuOverlay)
    menuOverlay.classList.add('overlay');
    menuOverlay.classList.add('is-visible');
    // Виключаю скролл
    scrollOf();

  } else {
    //Удаляю overlay
    menuOverlay.classList.remove('is-visible')
    // Включаю скролл
    scrollOn();

    setTimeout(()=> { 
      e.preventDefault();
      menuOverlay.remove(); 
    }, 500);
  }

})


